const gallery = document.querySelector(".gallery");


// var lightbox = new SimpleLightbox('.gallery a', { nav:true});


function createImgs (images, elem){
    elem.innerHTML ="";
    const imgs = images.reduce((acc, {preview,original,description}) => {

        return acc+`<li class="gallery-item">
                        <a class="gallery-link" href=${original}>
                            <img
                                class="gallery-image"
                                src= ${preview}
                                alt= '${description}'
                                width = "360"
                                height = "200"
                            />
                        </a>
                    </li>`
                       
    },"");

   elem.innerHTML = imgs;
}

createImgs(images, gallery)


var lightbox = new SimpleLightbox('.gallery a', {
  captionSelector: 'img',
  captions: true,
  captionType: 'attr',
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
  overlayOpacity: 1,
  className: 'custom-lightbox',
  docClose:	true,
  animationSpeed:	500,
  // animationSlide:	true,
  fadeSpeed:	500,
  rtl:	true,
})


const buttonArrow = document.querySelector("button")
  lightbox.on('shown.simplelightbox', function () {
	const buttons = document.querySelectorAll("button");
  const counter = document.querySelector(".sl-counter");
  buttons.forEach(value => value.style.color ="white");
  counter.style.color ="white"

});